import React, { ReactElement } from "react"
import { Link } from "gatsby"
import Layout from "../../components/layout"
import InlineCode from "../../components/inlineCode"
import Examples from "../../components/codeExamples"
import { ValidatorFunction, CodeExample } from "../../components/validatorFuncs"

function CommonFunctions(): ReactElement {
  return (
    <Layout
      title="Common functions"
      description="Validation functions for use on class and IEquatable<T> types"
    >
      <h1>Common Functions</h1>
      <p>
        <ul className="list">
          {["NotNull", "Null", "Equal", "NotEqual"].map((func) => (
            <li>
              <Link to={`/functions/common/#${func}`}>{func}</Link>
            </li>
          ))}
        </ul>
      </p>
      <hr className="my-6" />
      <ValidatorFunction
        name="NotNull"
        description="Ensures the value being validated is not null."
        methods={[
          `NotNull<T>()`,
          `NotNull<T, P>(Expression<Func<T, P>> selector)`,
        ]}
        brokenRule="Value cannot be null."
      >
        <CodeExample
          label="Using type instance."
          code={Examples.Common.NotNull.Example1}
        />
        <CodeExample
          label="Using selector expression."
          code={Examples.Common.NotNull.Example2}
        />
        <CodeExample
          label={
            <>
              Using <InlineCode>For</InlineCode> function.
            </>
          }
          code={Examples.Common.NotNull.Example3}
        />
      </ValidatorFunction>
      <hr className="my-6" />
      <ValidatorFunction
        name="Null"
        description="Ensures the value being validated is null."
        methods={[`Null<T>()`, `Null<T, P>(Expression<Func<T, P>> selector)`]}
        brokenRule="Value must be null."
      >
        <CodeExample
          label="Using type instance."
          code={Examples.Common.Null.Example1}
        />
        <CodeExample
          label="Using selector expression."
          code={Examples.Common.Null.Example2}
        />
        <CodeExample
          label={
            <>
              Using <InlineCode>For</InlineCode> function.
            </>
          }
          code={Examples.Common.Null.Example3}
        />
      </ValidatorFunction>
      <hr className="my-6" />
      <ValidatorFunction
        name="Equal"
        description="Ensures the value being validated is equal to a specified value."
        methods={[
          `Equal<T>(T other)`,
          `Equal<T, P>(Expression<Func<T, P>> selector, P other)`,
        ]}
        brokenRule="Value must equal '{other}'."
      >
        <CodeExample
          label="Using type instance."
          code={Examples.Common.Equal.Example1}
        />
        <CodeExample
          label="Using selector expression."
          code={Examples.Common.Equal.Example2}
        />
        <CodeExample
          label={
            <>
              Using <InlineCode>For</InlineCode> function.
            </>
          }
          code={Examples.Common.Equal.Example3}
        />
      </ValidatorFunction>
      <hr className="my-6" />
      <ValidatorFunction
        name="NotEqual"
        description="Ensures the value being validated is not equal to a specified value."
        methods={[
          `NotEqual<T>(T other)`,
          `NotEqual<T, P>(Expression<Func<T, P>> selector, P other)`,
        ]}
        brokenRule="Value must not equal '{other}'."
      >
        <CodeExample
          label="Using type instance."
          code={Examples.Common.NotEqual.Example1}
        />
        <CodeExample
          label="Using selector expression."
          code={Examples.Common.NotEqual.Example2}
        />
        <CodeExample
          label={
            <>
              Using <InlineCode>For</InlineCode> function.
            </>
          }
          code={Examples.Common.NotEqual.Example3}
        />
      </ValidatorFunction>
    </Layout>
  )
}

export default CommonFunctions
